<template>
  <div
    v-if="recommendedPrice || lowPrice"
    class="product-price-message"
  >
    <div
      v-if="recommendedPrice"
      class="product-price-message__content body-2"
    >
      <span v-html="pricingMessage('recommendedPrice', $string('product.form.recommended_price_html'))" />
      <tooltip
        :message="$string('product.form.recommended_price_tooltip_html')"
        :icon="'tooltip'"
      />
    </div>
    <div
      v-if="lowPrice"
      class="product-price-message__content body-2"
    >
      <span v-html="pricingMessage('lowestPrice', $string('product.form.lowest_price_html'))" />
      <tooltip
        :message="$string('product.form.lowest_price_tooltip_html')"
        :icon="'tooltip'"
      />
    </div>
  </div>
</template>

<script>

/**
 * Vue: Product price message (product-price-message)
 * -----------------------------------------------------------------------------
 * Message used to display lowest and recommended product price for the EU pricing directive feature.
 *
 */

import Tooltip from '~global/tooltip/tooltip'

export default {
  name: 'ProductPriceMessage',
  components: {
    Tooltip,
  },

  props: {
    lowestPrice: {
      type: [Boolean, String],
      default: false,
    },
    recommendedPrice: {
      type: [Boolean, String],
      default: false,
    },
  },

  data() {
    const lowPrice = this.lowPriceSetup()

    return {
      global: true,
      lowPrice,
    }
  },

  mounted() {
    // Not empty
  },

  methods: {

    /**
     * Get pricing message for recommended and lowest price
     */
    pricingMessage(type, string) {
      if (!this[type] || this[type] === '') {
        return
      }

      const selectedPrice = this[type]
      const strikePrice = type.includes('lowest')
        ? this.$variable('settings.pricingDirective.strikeLowestPrice')
        : this.$variable('settings.pricingDirective.strikeRecommendedPrice')

      const message = string.replace(
        '{{ vat }}',
        `
          <span class="product-price-message__vat">
            ${this.$string('product.form.vat_included')}
          </span>
        `,
      )

      const price = strikePrice ? `<s>${selectedPrice}</s>` : selectedPrice

      return message.replace( '{{ price }}', price)
    },

    lowPriceSetup() {
      if (!this.lowestPrice) {
        return false
      }

      if (typeof this.lowestPrice === 'string' && this.lowestPrice === '_DELETE_') {
        return false
      }

      return this.lowestPrice
    },
  },
}
</script>

<style lang="scss">
@import './product-price-message';
</style>
