<template>
  <div class="postal-code-validator">
    <div
      class="postal-code-validator__title body-2"
      v-text="`${$string('postal_code_validator.title')}`"
    />
    <form
      class="postal-code-validator__form input-group"
      @submit.prevent="handleSubmit"
    >
      <div class="postal-code-validator__input-wrapper">
        <input
          v-model="inputZip"
          type="text"
          :placeholder="`${$string('postal_code_validator.input_placeholder')}`"
          :class="`postal-code-validator__input ${
            isFormSubmitted && (!isZipValid || result === false) ? 'input-error' : ''
          } ${
            isFormSubmitted && result ? 'input-success' : ''
          }`"
          maxlength="4"
          required
          @keydown="handleInputKeydown"
          @change="handleInputChange"
        >
        <button
          v-if="isFormSubmitted && (!isZipValid || result === false)"
          class="postal-code-validator__clear"
          @click="clearForm"
        >
          <IconCloseFill />
        </button>
        <div
          v-else-if="isFormSubmitted && result"
          class="postal-code-validator__check"
        >
          <IconCheckmark />
        </div>
      </div>
      <button
        class="button button--secondary button--center postal-code-validator__submit"
        type="submit"
        :disabled="isFormSubmitted"
        v-text="`${$string('postal_code_validator.submit_button_label')}`"
      />
    </form>
    <p
      v-if="isFormSubmitted && !isZipValid"
      class="postal-code-validator__error-message"
      v-text="`${$string('postal_code_validator.error_message')}`"
    />
    <p
      v-if="isLoading"
      class="postal-code-validator__loading-message"
      v-text="`${$string('postal_code_validator.loading_message')}`"
    />
    <p
      v-if="isFormSubmitted && result === false"
      class="postal-code-validator__error-message"
      v-text="`${$string('postal_code_validator.invalid_zip_message')}`"
    />
    <div
      v-if="isFormSubmitted && result === true"
      class="postal-code-validator__success-notice"
    >
      <div
        class="postal-code-validator__success-message"
        v-text="`${$string('postal_code_validator.valid_zip_message')}`"
      />
      <button
        @click="clearForm"
      >
        <IconClose />
      </button>
    </div>
  </div>
</template>

<script>

/**
 * Vue: postal code validator (postal-code-validator)
 * -----------------------------------------------------------------------------
 * postal code validator.
 *
 */

import IconCloseFill from '~icons/misc/close--fill.svg'
import IconClose from '~icons/misc/close.svg'
import IconCheckmark from '~icons/misc/checkmark.svg'

export default {
  name: 'PostalCodeValidator',

  components: {
    IconClose,
    IconCloseFill,
    IconCheckmark,
  },

  props: {
    initialZip: {
      type: String,
      default: '',
      required: false,
    },
  },

  data() {
    return {
      inputZip: this.initialZip,
      isChangedByUser: false, // First input change makes this true
      isFormSubmitted: false, // True after submission, until a change is made
      isLoading: false, // True after submission if zip is valid, until a change is made
      result: null, // Postal code validity result
    }
  },

  computed: {
    isZipValid() {
      const zipRegex = /^(?:(?:[2-8]\d|9[0-7]|0?[28]|0?9(?=09))(?:\d{2}))$/
      return zipRegex.test(this.inputZip)
    },

    /**
     * The postal code app's domain name, used for API calls
     * @returns {Boolean}
     */
    appDomain() {
      return cnvs.credentials.hyperx_stacklet.domain
    },
  },

  methods: {
    clearForm() {
      this.isFormSubmitted = false
      this.isChangedByUser = false
      this.isLoading = false
      this.result = null
      this.inputZip = ''
    },

    handleInputChange() {
      this.isChangedByUser = true
    },

    handleInputKeydown() {
      this.isFormSubmitted = false
      this.isLoading = false
      this.result = null
    },

    async handleSubmit() {
      if (!this.isFormSubmitted) {
        this.isFormSubmitted = true
        this.result = null

        if (this.isZipValid) {
          this.isLoading = true
          // Get postal code validity
          this.result = await fetch(`https://${
            this.appDomain
          }/api/postcode-validator/postcode-lookup?postcode=${this.inputZip}`)
            .then((result) => result.json())
            .catch(() => false) // In case of total failure
          this.isLoading = false
        }
      }
    },
  },
}
</script>
