<template>
  <span v-html="textWithIcon" />
</template>

<script>

/**
 * Vue: Text with icon (text-with-icon)
 * -----------------------------------------------------------------------------
 * Converts icon shorthand codes into an icon (e.g. %icon_arrow%)
 *
 * @param {String} [text] - Text containing the icon code.
 */

export default {
  name: 'TextWithIcon',

  props: {
    text: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      global: true,
    }
  },

  computed: {
    iconString() {
      if (!this.text) {
        return
      }

      const matches = this.text.match(/%icon_(.+)%/)
      return matches && matches[0]
    },

    iconName() {
      if (!this.text) {
        return
      }

      const matches = this.text.match(/%icon_(.+)%/)
      return matches && matches[1]
    },

    iconUrl() {
      if (!this.iconName) {
        return
      }

      return this.$variable('urls.files')
        .replace('placeholder', `icon_${this.iconName}.svg`)
    },

    iconMarkup() {
      if (!this.text) {
        return
      }

      return `
        <span
          v-if="textWithIcon"
          class="icon"
          :class="[\`icon__${this.iconName}\`]"
        >
          <img src="${this.iconUrl}" />
        </span>
      `
    },

    textWithIcon() {
      if (!this.iconMarkup) {
        return this.text
      }

      return this.text.replaceAll(this.iconString, this.iconMarkup)
    },
  },
}
</script>
