<template>
  <div
    ref="siteHeader"
    class="site-header-container"
    js-site-header="container"
  >
    <header
      ref="siteHeader"
      class="site-header"
      data-sign-post-priority="1"
      :js-sign-post="$string('accessibility.sign_posts.site_header')"
      @keydown.esc="toggleSearch(false)"
    >
      <announcement-bar />

      <div class="site-header__container">
        <button
          v-if="isMobile"
          class="site-header__hamburger icon-button"
          :aria-label="$string('general.site_header.menu')"
          @click="componentLoad()"
        >
          <span class="hamburger">
            <icon-close v-if="getActiveOverlay === 'menuDrawer'" />
            <icon-hamburger v-else />
          </span>
        </button>

        <h1
          v-if="isHomepage"
          class="visually-hidden"
          v-text="$variable('store.shopName')"
        />

        <a
          :href="$variable('routes.root')"
          class="site-header__logo"
          :aria-label="$variable('store.shopName')"
        >
          <icon-logo />
        </a>

        <nav
          v-if="!isMobile"
          class="site-header__nav"
        >
          <ul class="site-header__nav-list">
            <li
              v-for="(tier1, index) in desktop"
              :key="tier1.handle"
              class="site-header__nav-item"
              :class="[
                isFocused(tier1.handle) ? [$class('focused')] : '',
              ]"
              @mouseover="setFocused(tier1)"
              @mouseout="setFocused(null)"
            >
              <a
                class="site-header__nav-link"
                :class="{
                  'site-header__nav-link-children': tier1.children
                }"
                :href="tier1.url"
                v-text="tier1.title"
              />
              <button
                v-if="tier1.children"
                :id="`${tier1.handle}-button`"
                class="site-header__nav-link-toggle"
                :aria-expanded="tier1.handle === focusedItem.handle"
                aria-haspopup="true"
                :aria-controls="tier1.handle"
                :aria-label="tier1.title"
                @click="setFocusedToggle(tier1)"
              >
                <icon-caret-down />
              </button>
              <nav
                v-if="tier1.children"
                :id="tier1.handle"
                :class="[
                  {'site-header__single-nav': tier1.levels === 1},
                  {'site-header__meganav': tier1.levels > 1},
                ]"
                role="menu"
                :aria-labelledby="`${tier1.handle}-button`"
              >
                <ul
                  v-if="tier1.levels === 1"
                  class="site-header__single-nav-list"
                >
                  <li
                    v-for="tier2 in tier1.children"
                    :key="tier2.handle"
                    role="presentation"
                    class="site-header__single-nav-item"
                  >
                    <a
                      class="site-header__single-nav-link"
                      role="menuitem"
                      :href="tier2.url"
                    >
                      <span
                        v-if="megaNavIcon(tier2.title)"
                        class="icon"
                      >
                        <img
                          :src="megaNavIcon(tier2.title)"
                          alt=""
                        >
                      </span>
                      <span v-text="megaNavTitle(tier2.title)" />
                    </a>
                  </li>
                </ul>

                <div
                  v-if="tier1.levels > 1"
                  class="site-header__meganav-wrapper"
                >
                  <div
                    class="site-header__meganav-menu-wrapper"
                  >
                    <ul
                      v-for="column in tier1.children"
                      :key="column.handle"
                      class="site-header__meganav-list"
                    >
                      <li
                        v-for="tier2 in column.children"
                        :key="tier2.handle"
                        role="presentation"
                        class="site-header__meganav-item"
                      >
                        <a
                          class="site-header__meganav-link"
                          role="menuitem"
                          :href="tier2.url"
                        >
                          <span
                            v-if="megaNavIcon(tier2.title)"
                            class="icon"
                          >
                            <img
                              :src="megaNavIcon(tier2.title)"
                              alt=""
                            >
                          </span>
                          <span v-text="megaNavTitle(tier2.title)" />
                          <span
                            v-if="settings.showArrows"
                            class="site-header__meganav-dir"
                          >
                            <icon-arrow-up-right />
                          </span>
                        </a>
                      </li>
                    </ul>
                  </div>
                  <a
                    v-if="getBanner(index).image"
                    class="site-header__meganav-banner"
                    :href="getBanner(index).link"
                  >
                    <div
                      class="site-header__meganav-image"
                      :style="{'background-image': `url(${getBanner(index).image})`}"
                    />
                    <span
                      class="site-header__meganav-tagline text-link"
                      v-text="getBanner(index).tagline"
                    />
                    <span
                      class="site-header__meganav-title"
                      v-text="getBanner(index).title"
                    />
                    <span class="site-header__meganav-link text-link">
                      <span v-text="$string('general.site_header.banner_link')" />
                      <icon-arrow-up-right />
                    </span>
                  </a>
                </div>
              </nav>
            </li>
          </ul>
        </nav>

        <a
          v-if="!isMobile && settings.showSearch"
          class="site-header__utility first"
          href="javascript:void(0)"
          :aria-label="$string('search.title')"
          @click="toggleSearch()"
        >
          <span class="icon-wrap">
            <icon-search />
          </span>
        </a>

        <div
          v-show="!isMobile && searchOpen"
          class="site-header__search"
        >
          <button
            class="site-header__search-close"
            :aria-label="$string('accessibility.close')"
            @click="toggleSearch(false)"
          >
            <icon-close />
          </button>
          <search-bar />
        </div>

        <a
          class="site-header__utility"
          :class="{
            'indicator': !cartEmpty,
          }"
          :href="$variable('routes.cart.url')"
          :aria-label="$string('general.site_header.cart')"
          @click="handleCartToggle"
          @mousedown="loadComponent('cart-drawer')"
        >
          <span
            class="visually-hidden"
            v-text="cart.item_count"
          />
          <span class="icon-wrap">
            <icon-cart />
          </span>
        </a>
      </div>

      <div
        v-if="!isMobile"
        class="site-header__overlay"
        :class="{ [$class('active')]: focusedItem.children || searchOpen }"
      />
    </header>

    <overlay
      key="menuDrawer"
      block-class="menu-drawer-overlay"
      direction="right"
      namespace="menuDrawer"
      :template="false"
      type="drawer"
    >
      <template #body="{ isActive }">
        <menu-drawer
          :handle="mobile.handle"
          :is-active="isActive"
          :item-count="mobile.itemCount"
        />
      </template>
    </overlay>
  </div>
</template>

<script>

/**
 * Component / Global: Site header
 * -----------------------------------------------------------------------------
 * Site header component, contains menu drawer and meganav.
 * - No style import as it's included in critical stylesheet.
 *
 */
import { defineAsyncComponent } from 'vue'
import { mapActions, mapGetters, mapState } from 'vuex'
import { debounce } from '~/helpers/general'
import AnnouncementBar from '~global/site-header/announcement-bar'
import SearchBar from '~global/search-bar/search-bar'

import IconLogo from '~icons/misc/logo.svg'
import IconCaretDown from '~icons/direction/caret/down.svg'

import IconSearch from '~icons/misc/search.svg'
import IconCart from '~icons/misc/cart.svg'
import IconClose from '~icons/misc/close.svg'
import IconArrowUpRight from '~icons/direction/arrow/up-right.svg'
import IconHamburger from '~icons/misc/hamburger.svg'

export default {
  name: 'SiteHeader',

  components: {
    AnnouncementBar,
    SearchBar,
    IconLogo,
    IconCaretDown,
    IconSearch,
    IconCart,
    IconClose,
    IconArrowUpRight,
    IconClose,
    IconHamburger,
    MenuDrawer: defineAsyncComponent({
      loader: () => import(
        /* webpackPrefetch: true */
        '~async/menu-drawer/menu-drawer'
      ),
    }),
  },

  props: {
    desktop: {
      type: Object,
      default: () => ({}),
    },
    mobile: {
      type: Object,
      default: () => ({}),
    },
    banners: {
      type: Object,
      default: () => ({}),
    },
    settings: {
      type: Object,
      default: () => ({}),
    },
    announcement: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {
      focusedItem: {
        handle: null,
        children: false,
      },
      searchOpen: false,
    }
  },

  computed: {

    /**
     * Map Vuex getters.
     */
    ...mapGetters({
      getActiveOverlay: 'overlays/getActiveOverlay',
    }),

    /**
     * Map Vuex state.
     */
    ...mapState({
      cart: (state) => state.cart.response,
      isMobile: (state) => state.index.screen.mobile,
      settings: (state) => state.index.settings,
    }),

    /**
     * Compute if cart is empty.
     * @returns {Boolean}
     */
    cartEmpty() {
      return this.cart.item_count === 0
    },

    isHomepage() {
      return this.$variable('request.pageType') === 'index'
    },
  },

  mounted() {
    this.handleResize()

    window.visualViewport.addEventListener(
      'resize',
      debounce(() => this.handleResize(), this.settings.throttle),
    )
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      loadComponent: 'components/load',
      toggleOverlay: 'overlays/toggle',
    }),

    componentLoad() {
      this.toggleOverlay({
        component: 'menu-drawer',
        ignoreDismissed: true,
        namespace: 'menuDrawer',
      })
    },

    /**
     * Handle cart toggle.
     * - Prevents opening cart drawer on cart page.
     * @param {Object} event - Click event.
     */
    handleCartToggle(event) {
      if (this.$variable('page.type') === 'cart') {
        return
      }

      event.preventDefault()

      this.toggleOverlay({
        component: 'cart-drawer',
        ignoreDismissed: true,
        namespace: 'cartDrawer',
      })
    },

    setFocusedToggle(item) {
      if (!item || item.handle === this.focusedItem.handle) {
        this.focusedItem.handle = null
        this.focusedItem.children = false
        return
      }

      this.focusedItem.handle = item.handle
      this.focusedItem.children = item.children
    },

    setFocused(item) {
      if (!item) {
        this.focusedItem.handle = null
        this.focusedItem.children = false
        return
      }

      this.focusedItem.handle = item.handle
      this.focusedItem.children = item.children
    },

    /**
     * Determine if menu item or child item is focused
     * @param {String} handle
     * @returns {Boolean}
     */
    isFocused(handle) {
      return this.focusedItem.handle === handle
    },

    /**
     * Get banner by menu item index.
     * @param {Number} number
     * @returns {Object}
     */
    getBanner(index) {
      const banner = this.banners.find((banner) => {
        return banner.index === index
      })

      if (!banner) {
        return false
      }

      return banner
    },

    /**
     * Parse Icon from string
     * @param {String} title
     * @returns {String}
     */
    megaNavIcon(title) {
      const src = title.substring(
        title.indexOf('%') + 1,
        title.lastIndexOf('%'),
      )

      if (!src.length) {
        return false
      }

      return cnvs.urls.files.replace('placeholder', `${src}.svg`)
    },

    /**
     * Parse title from string
     * @param {String} title
     * @returns {String}
     */
    megaNavTitle(title) {
      const re = new RegExp(/\%(.*?)\%/g)
      return title.replace(re, '')
    },

    /**
     * Toggle Search Interface
     */
    toggleSearch(state) {
      if (state !== undefined) {
        this.searchOpen = state
        return
      }

      this.searchOpen = !this.searchOpen
      cnvs.EventBus.emit('search:open', this.searchOpen)
    },

    handleResize() {
      document.documentElement.style.setProperty(
        '--full-header-height',
        `${this.$refs.siteHeader.offsetHeight}px`,
      )
    },
  },
}
</script>
