<template>
  <span class="tooltip-wrapper">
    <a
      class="tooltip-trigger"
      @mouseover="toggleDesktopTooltip"
      @mouseleave="toggleDesktopTooltip"
      @click.prevent="toggleTooltip"
    >
      <icon-tooltip v-if="icon === 'tooltip'" />
      <icon-info v-else />
    </a>
    <div
      v-if="displayTooltip"
      class="tooltip"
    >
      <p
        v-html="message"
      />
      <a
        v-if="isMobile"
        class="tooltip-close"
        @click.prevent="toggleTooltip"
      >
        <icon-close />
      </a>
    </div>
  </span>
</template>

<script>

/**
 * Vue: tooltip (tooltip)
 * -----------------------------------------------------------------------------
 * tooltip.
 *
 */
import IconInfo from '~icons/misc/info.svg'
import IconClose from '~icons/misc/close.svg'
import IconTooltip from '~icons/misc/tooltip.svg'

import { onMounted, onBeforeUnmount } from 'vue'
import { mapState } from 'vuex'

export default {
  name: 'Tooltip',

  components: {
    IconInfo,
    IconClose,
    IconTooltip,
  },

  props: {
    message: {
      type: String,
      default: '',
      required: true,
    },
    icon: {
      type: String,
      default: 'info',
    },
  },

  setup() {
    const outsideClickEvent = (event) => {
      const closeBtn = document.querySelector('.tooltip-close')
      const { target } = event

      if (!closeBtn || target.classList.contains('tooltip') || target.closest(".tooltip-wrapper")) {
        return
      }

      closeBtn.click()
    }

    onMounted(() => {
      document.addEventListener('click', outsideClickEvent)
    })

    onBeforeUnmount(() => {
      document.removeEventListener('click', outsideClickEvent)
    })
  },

  data() {
    return {
      displayTooltip: false,
      global: true,
    }
  },

  computed: {

    /**
     * Map Vuex state.
     */

    ...mapState({
      isMobile: (state) => state.index.screen.width <= 768,
    }),
  },

  mounted() {
    // Not empty
  },

  methods: {

    /**
     * Toggle tooltip for desktop
     */
    toggleDesktopTooltip(event) {
      if (this.isMobile) {
        return
      }

      const hoverEvent = event.type === 'mouseover' || event.type === 'mouseleave'
      if (hoverEvent) {
        this.displayTooltip = event.type === 'mouseover'
      }
    },

    /**
     * General on click tooltip display toggle for mobile
     */
    toggleTooltip() {
      if (!this.isMobile) {
        return
      }

      this.displayTooltip = !this.displayTooltip
    },
  },
}
</script>
