/**
 * Helper: Manipulate
 * -----------------------------------------------------------------------------
 * Manipulates objects and data.
 * - Add in alphabetical order.
 *
 */

/**
 * Combine two objects using properties as the override.
 * @param {Object} object1 - Original object.
 * @param {Object} object2 - New object, overrides existing object values.
 * @returns {Object}
 */
export function combineObjects(object1, object2) {
  for (const property in object2) {
    if (object2 != null && typeof object2 !== 'undefined') {
      object1[property] = object2[property]
    }
  }

  return object1
}

/**
 * Hashes a string using a 53-bit hash.
 * @param {String} str - The value to hash.
 * @param {Number} seed - Optional unsigned integer, 32-bit max
 * @returns {number}
 */
 export function cyrb53(str, seed = 0) {
  let h1 = 0xdeadbeef ^ seed
  let h2 = 0x41c6ce57 ^ seed
  for (let i = 0, ch; i < str.length; i++) {
    ch = str.charCodeAt(i)
    h1 = Math.imul(h1 ^ ch, 2654435761)
    h2 = Math.imul(h2 ^ ch, 1597334677)
  }
  h1 = Math.imul(h1 ^ (h1 >>> 16), 2246822507) ^ Math.imul(h2 ^ (h2 >>> 13), 3266489909)
  h2 = Math.imul(h2 ^ (h2 >>> 16), 2246822507) ^ Math.imul(h1 ^ (h1 >>> 13), 3266489909)
  // eslint-disable-next-line no-mixed-operators
  return 4294967296 * (2097151 & h2) + (h1 >>> 0)
}
