export default {
  namespaced: true,
  state: () => ({
    announcement: {},
    announcementBlock: [],
  }),

  getters: {

    getSettings(state) {
      return state.announcement
    },

    getBlocks(state) {
      return state.announcementBlock
    },
  },

  mutations: {

    /**
     * Set announcement settings.
     */
    SET_ANNOUNCEMENT_SETTINGS(state, announcement) {
      state.announcement = announcement
    },

    /**
     * Set announcement blocks.
     */
    SET_ANNOUNCEMENT_BLOCKS(state, announcementBlock) {
      state.announcementBlock = announcementBlock
    },
  },

  actions: {

    /**
     * Set announcement settings.
     * @param {Object} settings
     */
    setAnnouncementSettings({ commit }, settings) {
      commit('SET_ANNOUNCEMENT_SETTINGS', settings)
    },

    /**
     * Set announcement blocks.
     * @param {Array} blocks
     */
    setAnnouncementBlocks({ commit }, blocks) {
      commit('SET_ANNOUNCEMENT_BLOCKS', blocks)
    },
  },
}