<template>
  <div
    v-if="showResults"
    class="search-bar-results"
  >
    <div class="search-bar-results__content container">
      <template v-if="hasResults">
        <div
          v-if="!hasNoSuggestions"
          class="search-bar-results__suggestions"
          :class="[
            suggestions.length ? 'has-suggestions' : 'no-suggestions',
            collections.length ? 'has-collections' : 'no-collections',
            pages.length ? 'has-pages' : 'no-pages',
          ]"
        >
          <div
            v-show="suggestions.length"
            class="search-bar-results__section suggestions"
          >
            <h4
              class="search-bar-results__title subtitle-1"
              v-text="$string('search.suggestions')"
            />
            <ul>
              <li
                v-for="(suggestion, sIndex) in suggestions"
                :key="sIndex"
              >
                <a
                  class="search-bar-results__suggestion"
                  :href="`${$variable('routes.search')}?q=${suggestion.query}`"
                  v-html="suggestion._highlightResult.query.value"
                />
              </li>
            </ul>
          </div>

          <div
            v-show="collections.length"
            class="search-bar-results__section collections"
          >
            <h4
              class="search-bar-results__title subtitle-1"
              v-text="$string('search.collections')"
            />
            <ul>
              <li
                v-for="collection in collections"
                :key="collection.objectID"
              >
                <a
                  class="search-bar-results__suggestion"
                  :href="`${$variable('routes.root')}collections/${collection.handle}`"
                  v-html="collection._highlightResult.title.value"
                />
              </li>
            </ul>
          </div>

          <div
            v-show="pages.length"
            class="search-bar-results__section pages"
          >
            <h4
              class="search-bar-results__title subtitle-1"
              v-text="$string('search.pages')"
            />
            <ul>
              <li
                v-for="page in pages.slice(0,2)"
                :key="page.objectID"
              >
                <a
                  class="search-bar-results__suggestion"
                  :href="`${$variable('routes.root')}pages/${page.handle}`"
                  v-html="page._highlightResult.title.value"
                />
              </li>
            </ul>
          </div>
        </div>

        <div
          v-show="products.length"
          class="search-bar-results__results"
        >
          <product-card
            v-for="product in products.slice(0,4)"
            :key="product.id"
            class="search-bar-results__product"
            :is-algolia="true"
            :product="product"
          />
        </div>
      </template>
      <template v-else>
        <div
          class="search-bar-results__no-results"
          v-text="noResultsText"
        />
      </template>
    </div>

    <btn
      class="search-bar-results__see-all"
      modifiers="tertiary center"
      :label="$string('search.view_all_results_predictive')"
      :show-icon="false"
      @click="onSubmit()"
    />
  </div>
</template>

<script>

/**
 * Vue: Search bar results (search-bar-results)
 * -----------------------------------------------------------------------------
 * Search Results powered by Algolia.
 *
 */
import ProductCard from '~async/product-card/product-card'

export default {
  name: 'SearchBarResults',

  components: {
    ProductCard,
  },

  props: {
    currentRefinement: {
      type: String,
      default: '',
    },
    indices: {
      type: Object,
      default: () => ({}),
    },
    search: {
      type: Object,
      default: () => ({
        input: {},
      }),
    },
    query: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      global: true,
      results: 0,
    }
  },

  computed: {

    /**
     * Compute suggestions.
     */
    suggestions() {
      return this.index('products_query_suggestions')
    },

    /**
     * Compute collections.
     */
    collections() {
      return this.index('collections')
    },

    /**
     * Compute pages.
     */
    pages() {
      return this.index('pages')
    },

    /**
     * Compute products.
     */
    products() {
      return this.index('products')
    },

    /**
     * Compute no results text.
     */
    noResultsText() {
      return this.$string(
        'collection.filtering.no_results',
        { replace: { query: this.query } },
      )
    },

    /**
     * Compute if results should show
     */
    showResults() {
      if (
        this.hasResults ||
        this.query.length
      ) {
        this.resetReviews()
        return true
      }

      return false
    },

    /**
     * Compute if any results exist.
     */
    hasResults() {
      if (
        this.suggestions.length ||
        this.collections.length ||
        this.pages.length ||
        this.products.length
      ) {

        return true
      }

      return false
    },

    /**
     * Check if search results have suggestions.
     * @return {Boolean}
     */
    hasNoSuggestions() {
      return this.products.length &&
      (!this.suggestions.length &&
        !this.collections.length &&
        !this.pages.length
      )
    },
  },

  mounted() {
    // Not empty
  },

  methods: {

    /**
     * Get hits from algolia.
     * @param {String} selector
     * @return {Object}
     */
    index(selector) {
      const index = this.indices.find((index) => {
        return index.indexId === `${window.algoliaShopify.config.index_prefix}${selector}`
      })

      return index.hits.map((item) => ({
        ...item,
        compare_at_price: Number(item.compare_at_price )*100,
        price: Number(item.price )*100,
      }))
    },

    /**
     * On form submit.
     */
    onSubmit() {
      if (!this.query.length) {
        return
      }

      window.location.href = `${this.$variable('routes.search')}?q=${encodeURIComponent(this.query)}`
    },

    /**
     * Reset yotpo reviews with slight delay hack.
     */
    resetReviews() {
      if (window.yotpoWidgetsContainer) {
        setTimeout(() =>{
          window.yotpoWidgetsContainer?.initWidgets()
        }, 200)
      }
    },
  },
}
</script>

<style lang="scss">
@import './search-bar-results';
</style>