<template>
  <component
    :is="element"
    class="product-price__container"
    :class="[
      $blockClass(blockClass, 'prices'),
      { 'product-prices--reverse': reverse },
    ]"
  >
    <div class="product-price">
      <s
        v-if="formattedCompareAtPrice && !reverse && !enabledPricingDirective"
        class="product-price__compare"
        :class="[
          displayClass,
          comparePriceTextSize,
          $blockClass(blockClass, 'compare-at-price')
        ]"
        v-text="formattedCompareAtPrice"
      />

      <span
        v-if="showOriginalPrice"
        class="product-card__original-price"
      >
        <s>{{ $formatMoney(compareAtPriceVal) }}</s>
      </span>

      <span
        class="product-price__current h6"
        :class="[
          displayClass,
          priceTextSize,
          $blockClass(blockClass, 'price'),
          { 'product-price__sale-price': formattedCompareAtPrice },
          price && compareAtPrice ? 'is-sale' : '',
        ]"
        v-text="formattedPrice"
      />

      <s
        v-if="formattedCompareAtPrice && reverse && !enabledPricingDirective"
        class="product-price__compare-at-price"
        :class="[
          displayClass,
          comparePriceTextSize,
          $blockClass(blockClass, 'compare-at-price'),
        ]"
        v-text="formattedCompareAtPrice"
      />
    </div>
    <p
      v-if="enabledVatMessage"
      class="product-price__vat-message body-2"
      v-text="$string('product.form.vat_included')"
    />
  </component>
</template>

<script>

/**
 * Vue: Product prices
 * -----------------------------------------------------------------------------
 * Global product prices template.
 *
 * @param {Boolean} available - If the product is available.
 * @param {String} [blockClass] - Block level class to apply to all elements.
 * @param {String} [displayClass] - Block level class to apply to all price elements.
 * @param {Number|Boolean} compareAtPrice - Compare at price if it exists.
 * @param {String} [element] - Custom element override.
 * @param {Number} price - Product price, in cents format.
 * @param {Boolean} [reverse] - Reverse order of prices.
 *
 */
export default {
  name: 'ProductPrices',

  props: {
    available: {
      type: Boolean,
      default: true,
    },
    blockClass: {
      type: String,
      default: '',
    },
    displayClass: {
      type: String,
      default: '',
    },
    compareAtPrice: {
      type: [Number, Boolean],
      default: false,
      required: true,
    },
    element: {
      type: String,
      default: 'p',
    },
    price: {
      type: [Number, Boolean],
      default: 0,
      required: true,
    },
    reverse: {
      type: Boolean,
      default: false,
    },
    showOriginalPrice: {
      type: Boolean,
      default: false,
    },
    compareAtPriceVal: {
      type: [Number, Boolean],
      default: 0,
      required: true,
    },
    smallText: {
      type: Boolean,
      default: false,
    },
    disabledPricingDirective: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      global: true,
    }
  },

  computed: {

    /**
     * Compute compare at price template.
     * @returns {String}
     */
    formattedCompareAtPrice() {
      const hasCompareAtPrice =  this.compareAtPrice && this.price < this.compareAtPrice

      if (!hasCompareAtPrice && typeof this.compareAtPrice !== 'string') {
        return
      }

      const priceHere = hasCompareAtPrice
        ? this.$formatMoney(this.compareAtPrice)
        : this.compareAtPrice

      return hasCompareAtPrice
        ? this.$formatMoney(this.compareAtPrice)
        : this.compareAtPrice
    },

    /**
     * Compute price template.
     * @returns {String}
     */
    formattedPrice() {
      if (this.price <= 0) {
        return this.$string('product.prices.free')
      }

      return this.price ? this.$formatMoney(this.price) : ''
    },

    /**
     * Compute price template.
     * @returns {String}
     */
    enabledPricingDirective() {
      if (!this.disabledPricingDirective && this.$variable('settings.pricingDirective.enable')) {
        return true
      }

      return !this.disabledPricingDirective
    },

    enabledVatMessage() {
      return this.enabledPricingDirective && this.$variable('settings.pricingDirective.includeVatText')
    },

    comparePriceTextSize() {
      return this.smallText ? 'body-1' : 'h6'
    },

    priceTextSize() {
      return this.smallText ? 'subtitle-1' : 'h6'
    }
  },
}
</script>

<style lang="scss">
  @import './product-prices';
</style>
