<template>
  <div class="bundle">
    <div
      id="bundleAccordion1"
      class="bundle__control accordion__control subtitle-1"
      aria-controls="bundleContent"
      aria-expanded="false"
    >
      <h3
        v-if="headingText?.heading"
        class="bundle__title">
        {{ headingText.heading }}
        <icon-tag />
      </h3>

      <div class="bundle__header">
        <div
          v-if="headingText?.copy"
          class="bundle__paragraph"
        >
          <p
            class="body-2 bundle__copy"
            v-text="headingText.copy"
          />
        </div>

        <div
          v-if="bundleProducts.length > 3"
          class="bundle__navigation"
        >
          <div
            ref="swiperPrev"
            class="swiper-button-prev swiper-button-custom"
            js-product-carousel="prev"
          >
            <icon-chevron-left />
          </div>
          <div
            ref="swiperNext"
            class="swiper-button-next swiper-button-custom"
            js-product-carousel="next"
          >
            <icon-chevron-right />
          </div>
        </div>
      </div>
    </div>

    <swiper
      ref="bundleCarousel"
      class="product_bundle__carousel"
      :modules="modules"
      :breakpoints="swiper.breakpoints"
      :navigation="swiper.navigation"
      :observer="true"
      :observe-parents="true"
      :prevent-interaction-on-transition="true"
      :slides-per-view="3"
      :space-between="12"
      :threshold="5"
    >
      <swiper-slide
        v-for="(bundle, index) in bundleProducts"
        :key="index"
        class="bundle__slide"
      >
        <div class="checkbox__container">
          <input
            :id="`bundledProduct-${index}`"
            type="checkbox"
            class="checkbox__input"
            :value="bundle.discountedProduct.variants[0].id"
            :name="`bundledProduct-${index}`"
            aria-checked="false"
            @change="addBundle($event, bundle)"
          >

          <label
            :for="`bundledProduct-${index}`"
            class="checkbox__label"
          >
            <span
              v-if="bundle.discountType === 'percentage'"
              class="body badge badge--primary"
            >
              {{ getPercentText(bundle.discountAmount) }}
            </span>

            <span
              v-else
              class="body badge badge--primary"
            >
              {{ getAmountText($formatMoney(bundle.discountAmountSaved * 100)) }}
            </span>

            <product-card
              :product="bundle.discountedProduct"
              :has-bundle="true"
              :discount-amount="bundle.discountAmount"
              :discount-amount-saved="bundle.discountAmountSaved"
              :show-original-price="true"
              :discount-type="bundle.discountType"
            />
          </label>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Navigation } from 'swiper'
import IconTag from '~icons/misc/tag.svg'
import IconChevronLeft from '~icons/direction/chevron/left.svg'
import IconChevronRight from '~icons/direction/chevron/right.svg'
import ProductCard from '~async/product-card/product-card.vue'

export default {
  name: 'ProductBundles',

  components: {
    Swiper,
    SwiperSlide,
    IconTag,
    IconChevronLeft,
    IconChevronRight,
    ProductCard,
  },

  props: {
    product: {
      type: [Boolean, Object],
      default: false,
    },
  },

  emits: ['add-bundle'],

  data() {
    return {
      modules: [Navigation],
      swiper: {
        breakpoints: {
          0: {
            slidesPerView: 3,
            spaceBetween: 5,
          },
          768: {
            slidesPerView: 3,
            spaceBetween: 10,
          },
          1280: {
            slidesPerView: 3,
            spaceBetween: 5,
          },
        },
        navigation: {
          nextEl: '[js-product-carousel="next"]',
          prevEl: '[js-product-carousel="prev"]',
        },
      },
      matchingBundles: [],
      bundles: {
        ...window.cnvs.productBundles,
      },
    }
  },

  computed: {

    /**
     * Compute heading text.
     * - For instances where there could be mulitple bundles, then the first bundle
     * wil be used to display heading title and copy text
     */
    headingText() {
      const firstBundle = this.bundles.items.find((item) => {
        return (item.bundleType === 'single' && item.parentProductId === this.product.id) ||
        (item.bundleType === 'category' && item.bundleCategory === this.product.type)
      })

      if (!firstBundle) {
        return false
      }
      const {copy, heading} = firstBundle
      return {copy, heading}
    },

    /**
     * Get all matching bundle products.
     * - Combines products from multiple bundles
     */
    bundleProducts() {
      const allBundles = []
      const matchingBundles = []

      if (Object.keys(this.bundles).length < 1) {
        return
      }

      this.bundles.items.forEach((item) => {
        if (item.bundleType === 'category' && item.bundleCategory === this.product.type) {
          allBundles.push(item)
          return
        }

        if (item.bundleType === 'single' && item.parentProductId === this.product.id) {
          allBundles.push(item)
        }
      })

      // Flattens all bundles into single bundle array
      allBundles.forEach((bundle) => {
        bundle.discountedProducts.forEach((discountProduct) => {
          if (!discountProduct.discountedProduct.available) {
            return
          }

          matchingBundles.push({
            discountedProduct: discountProduct.discountedProduct,
            swatchUrls: discountProduct.swatchUrls,
            discountAmount: bundle.discountAmount,
            discountType: bundle.discountType,
            discountAmountSaved: bundle.discountAmountSaved,
          })
        })
      })

      return matchingBundles
    }
  },

  methods: {

    /**
     * Get percentage discount text.
     * @param {String} discount - Item percent discount
     * @returns {String}
     */
    getPercentText(discount) {
      return this.$string('product_bundles.percent_off').replace('{{amount}}', discount)
    },

    /**
     * Get discount amount text.
     * @param {String} discount - Item discount amount
     * @returns {String}
     */
    getAmountText(discount) {
      return this.$string('product_bundles.amount_off').replace('{{amount}}', discount)
    },


    /**
     * Include bundle data to add to cart
     * @param {Object} event - Click event.
     */
    addBundle(event, discountProduct) {
      const checked = event.target.checked ? true : false
      const properties = {
        _bundleAddOn: true,
      }

      this.$emit('add-bundle', {
        id: discountProduct.discountedProduct.variants[0].id,
        properties,
        quantity: 1,
        checked,
      })
    },
  },
}
</script>

<style lang="scss">
@import './product-bundles';
@import '~/../styles/components/checkbox';
</style>