<template>
  <div
    v-if="announcement.enable"
    :class="[
      'site-header__announcement',
      'announcement-bar',
      'announcement-bar--primary',
      `announcement-bar--${announcement.layout}`,
      'is-active',
      `${!blocks ? 'mobile-hide' : ''}`,
    ]
    "
    role="dialog"
    js-site-header="announcementWrapper"
  >
    <div class="announcement-bar__inner container">
      <div class="announcement-bar__list-wrapper">
        <ul
          ref="announcementCarousel"
          class="announcement-bar__list"
        >
          <li
            v-for="(block, index) in blocks"
            :key="index"
            class="announcement-bar__list-item"
            :class="index === 0 ? 'active' : ''"
            v-html="updateCarouselItemHtml(block.title)"
          />
        </ul>
        <button
          v-if="blocks && blocks.length> 1"
          class="announcement-bar__list-icon left icon"
          @click.prevent.stop="() => switchAnnouncementManually(-1)"
        >
          <icon-chevron-right />
        </button>
        <button
          v-if="blocks && blocks.length> 1"
          class="announcement-bar__list-icon right icon"
          @click.prevent.stop="() => switchAnnouncementManually(1)"
        >
          <icon-chevron-right />
        </button>
      </div>

      <store-selector
        custom-class="announcement-bar"
      />
    </div>
  </div>
  <overlay
    key="storeSwitcher"
    block-class="store-switcher"
    direction="up"
    namespace="storeSwitcher"
    type="modal"
    :teleport="false"
  >
    <template #body>
      <h3
        class="store-switcher__title h5"
        v-text="$string('general.store_switcher.title')"
      />
      <p
        class="store-switcher__subtitle"
        v-text="$string('general.store_switcher.subtitle')"
      />
      <form
        class="store-switcher__form"
        @submit.prevent="submitSwitcher()"
      >
        <select
          id="store-switcher-country"
          v-model="country"
          class="store-switcher__select"
          name="store-switcher-country"
          :aria-label="$string('accessibility.country_selector.select_country_label')"
          :style="{ backgroundImage: `url(${selectArrow})` }"
        >
          <option
            v-for="(value, key, index) in countries"
            :key="index"
            :value="key"
            :selected="key === country"
            v-text="value"
          />
        </select>

        <select
          id="store-switcher-language"
          v-model="language"
          class="store-switcher__select"
          name="store-switcher-language"
          :aria-label="$string('accessibility.country_selector.select_language_label')"
          :style="{ backgroundImage: `url(${selectArrow})` }"
        >
          <option
            selected
            disabled
            v-text="$string('general.store_switcher.placeholder_lang')"
          />
          <option
            v-for="(key, index) in supportedLanguages"
            :key="index"
            :value="key"
            v-text="languages[key]"
          />
        </select>

        <btn
          class="store-switcher__submit"
          type="submit"
          :label="$string('general.store_switcher.update')"
          modifiers="primary"
        />
      </form>
    </template>
  </overlay>
</template>

<script>

/**
 * Vue: Announcement bar (announcement-bar)
 * -----------------------------------------------------------------------------
 * Announcement Bar to be rendered within the Site Header.
 *
 */
import { mapActions, mapGetters } from 'vuex'
import { stores, countries, languages } from '~/helpers/multi-store'

import Btn from '~global/btn/btn'
import StoreSelector from '~global/store-selector/store-selector'
import IconChevronRight from '~icons/direction/caret/right.svg'

export default {
  name: 'AnnouncementBar',

  components: {
    Btn,
    StoreSelector,
    IconChevronRight,
  },

  props: {},

  data() {
    return {
      stores: [],
      languages: {},
      countries: {},
      language: 'EN',
      country: 'GB',
      nextSlide: -1,
      announcementSliderInterval: '',
    }
  },

  computed: {

    /**
     * Compute announcement bar text
     */
    announcementText() {
      if (!this.announcement.text) {
        return '&nbsp'
      }

      return this.announcement.text
    },

    /**
     * Compute default country.
     */
    defaultCountry() {
      const country = {
        code: this.country,
        name: this.countries[this.country],
      }

      return country
    },

    /**
     * Compute default language.
     */
    defaultLanguage() {
      const language = {
        code: this.language,
        name: this.languages[this.language],
      }

      return language
    },

    /**
     * Compute supported languages from chosen store.
     */
    supportedLanguages() {
      const store = this.getStore()
      const languages = this.getLanguages(store)

      return languages
    },

    /**
     * Compute arrow icon for select.
     */
    selectArrow() {
      return cnvs.urls.assets.replace('placeholder', 'icon-caret-down.svg')
    },

    /**
     * Map Vuex getters.
     */
    ...mapGetters({
      announcement: 'announcement/getSettings',
      blocks: 'announcement/getBlocks',
    }),
  },

  mounted() {
    this.stores = stores
    this.languages = languages
    this.countries = countries

    this.setDefaultLanguage()
    this.announcementsAutoSwitch()
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      openOverlay: 'overlays/open',
    }),

    /**
     * Open Store Switcher overlay
     */
    openSwitcher() {
      this.openOverlay({
        ignoreDismissed: true,
        namespace: 'storeSwitcher',
      })
    },

    /**
     * Add class to the announcement bar items
     */
    updateCarouselItemHtml (html) {
      return html.replace('<p', '<p class="announcement-bar__list-item-text"')
    },

    /**
     * Get store based on selected country
     * @returns {Object}
     */
    getStore() {
      let code

      switch(this.country) {
        case 'CA':
        case 'US':
        case 'GB':
        case 'DE':
        case 'FR':
        case 'AU':
          code = this.country
          break
        default:
          code = 'ROW'
      }

      const store = stores.find((store) => {
        return store.storeCode === code
      })

      return store
    },

    /**
     * Get store languages.
     * @param {Object} store
     * @returns {Array}
     */
    getLanguages(store) {
      return store.supportedLanguages.replaceAll('-', '').replaceAll(' ', '').split(',')
    },

    /**
     * Redirect to appropriate store on submit.
     */
    submitSwitcher(e) {
      const store = this.getStore()
      const languages = this.getLanguages(store)
      const { pathname } = window.location
      const currentPath = pathname === "/" || pathname === '/en'  ? '' : pathname
      let url = `${store.storeUrl.replace(/\/$/,"")}`

      if (languages.length > 1 && this.language !== store.defaultLanguage) {
        const lang = this.language.toLowerCase()
        url += `/${lang}`
      }

      url += currentPath.replace('/en', '')

      if (store.storeCode === "ROW") {
        url += `/?loc=${this.country}`
      }

      window.location.href = url
    },

    /**
     * Sets initial default language.
     */
    setDefaultLanguage() {
      if (!cnvs.store) {
        return
      }

      this.language = cnvs.store.language.toUpperCase()
      this.country = cnvs.store.country
    },

    /**
     * Starts announcement animation
     */
    announcementsAutoSwitch () {
      const that = this

      if (this.blocks.length <= 1) { return }

      this.announcementSliderInterval = setInterval(function () {
        that.switchAnnouncementSlide(1)
      }, this.announcement.timing)
    },

    /**
     * Switch announcement on click
     * @param {Int} nextSlideDirection
     */
    switchAnnouncementManually (nextSlideDirection) {
      clearInterval(this.announcementSliderInterval)

      this.switchAnnouncementSlide(nextSlideDirection)
      this.announcementsAutoSwitch()
    },

    /**
     * Calculates which announcement to show
     * @param {Int} nextSlideDirection
     */
    switchAnnouncementSlide (nextSlideDirection) {
      const carousel = this.$refs?.announcementCarousel
      const slideToShow = this.nextSlide + nextSlideDirection

      if (slideToShow >= carousel.children.length) {
        this.nextSlide = 0
      } else if (slideToShow < 0) {
        this.nextSlide = carousel.children.length - 1
      } else {
        this.nextSlide = slideToShow
      }

      for (let i = 0; i < carousel.children.length; i++) {
        const element = carousel.children[i]

        if (this.nextSlide == i && !element.classList.contains('active')) {
          element.classList.add('active')
        } else if (this.nextSlide != i && element.classList.contains('active')) {
          element.classList.remove('active')
        }
      }
    },
  },
}
</script>
