<template>
  <div class="announcement-bar-settings" />
</template>

<script>

/**
 * Vue: announcement-bar-settings (announcement-bar-settings)
 * -----------------------------------------------------------------------------
 * Settings for announcement bar including block for carousel.
 * - Static section.
 *
 */
import { mapActions } from 'vuex'

export default {
  name: 'AnnouncementBarSettings',

  props: {
    blocks: {
      type: Array,
      default: () => [],
    },
    announcement: {
      type: Object,
      default: () => {},
    },
  },

  data() {
    return {
      cnvsGlobal: true,
    }
  },

  mounted() {
    this.setAnnouncementSettings(this.announcement)
    this.setAnnouncementBlocks(this.blocks)
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      setAnnouncementSettings: 'announcement/setAnnouncementSettings',
      setAnnouncementBlocks: 'announcement/setAnnouncementBlocks',
    }),
  },
}
</script>

<style lang="scss">
@import './announcement-bar-settings';
</style>
