/**
 * Helper: Format
 * -----------------------------------------------------------------------------
 * Formats values and objects.
 * - Add in alphabetical order.
 *
 */
import { formatMoney } from '@shopify/theme-currency'

import { convertToHandle } from '~/helpers/convert'
import { decodeApiId, transformEdges } from '~/helpers/graphql'

/**
 * Formats response from product to match liquid object.
 * @param {Object} response - GraphQL response.
 * @returns {Object}
 */
 export function formatProductToLiquid(product) {
  const formattedProduct = {
   available: product.available,
   compare_at_price: (Number(product.compareAtPriceRange?.maxVariantPrice.amount) * 100) || false,
   compare_at_price_max: (Number(product.compareAtPriceRange?.maxVariantPrice.amount) * 100) || false,
   compare_at_price_min: (Number(product.compareAtPriceRange?.minVariantPrice.amount) * 100) || false,
   compare_at_price_varies: (product.compareAtPriceRange?.maxVariantPrice.amount !== product.compareAtPriceRange?.minVariantPrice.amount),
   content: product.descriptionHtml,
   created_at: null, //unavailable
   description: product.descriptionHtml,
   handle: product.handle,
   id: decodeApiId(product.id),
   images: formatImagesToLiquid(product),
   media: [], // TODO
   options: formatOptionsToLiquid(product),
   price: (Number(product.priceRange?.minVariantPrice.amount) * 100) || false,
   price_max: (Number(product.priceRange?.maxVariantPrice.amount) * 100) || false,
   price_min: (Number(product.priceRange?.minVariantPrice.amount) * 100) || false,
   price_varies: (product.priceRange?.maxVariantPrice.amount !== product.priceRange?.minVariantPrice.amount),
   published_at: null, //unavailable
   requires_selling_plan: null, //unavailable
   selling_plan_groups: [], //unavailable
   tags: product.tags,
   title: product.title,
   type: product.productType,
   variants: [],
   vendor: product.vendor,
  }

  /**
   * Add additional values which rely on formatted response.
   */
   const isGiftCard = formattedProduct.type === 'Gift Cards'
   formattedProduct.featured_image = formattedProduct.images[0]
   formattedProduct.variants = formatVariantsToLiquid(product, isGiftCard)

  return formattedProduct
}

/**
 * Formats images to match liquid object.
 * @param {Object} response - GraphQL response.
 * @returns {Array}
 */
 function formatImagesToLiquid(product) {
  return transformEdges(product.images).map((image) => {
    return image.url
  })
}

/**
 * Formats options to match liquid object.
 * @param {Object} response - GraphQL response.
 * @returns {Array}
 */
 function formatOptionsToLiquid(product) {
  return product.options.map((option) => {
    return option.name
  })
}

/**
 * Formats variants to match liquid object.
 * @param {Object} response - GraphQL response.
 * @param {Boolean} isGiftCard - Product type is gift card.
 * @returns {Array}
 */
 function formatVariantsToLiquid(product, isGiftCard) {
  return transformEdges(product.variants).map((variant) => {
    const formattedVariant = {
      available: variant.available,
      barcode: null, //unavailable
      compare_at_price: (Number(variant.compareAtPriceV2?.amount) * 100) || false,
      featured_image: getVariantImage(variant, product),
      id: decodeApiId(variant.id),
      inventory_management: null, //unavailable
      name: variant.title,
      option1: variant.selectedOptions[0]?.value,
      option2: variant.selectedOptions[1]?.value,
      option3: variant.selectedOptions[2]?.value,
      options: variant.selectedOptions.map((option) => {
        return option.value
      }),
      price: (Number(variant.priceV2.amount) * 100) || false,
      public_title: null, //unavailable
      requires_selling_plan: null, //unavailable
      requires_shipping: null, //unavailable
      selling_plan_allocations: null, //unavailable
      sku: variant.sku,
      taxable: null, //unavailable
      title: variant.title,
      weight: variant.weight,
    }

    return formattedVariant
  })
}

/**
 * Formats response from product.
 * @param {Object} response - GraphQL response.
 * @returns {Object}
 */
export function formatProduct(response) {
  const product = response.product

  const url = cnvs.routes.root === '/'
    ? `//${location.host}/products/${product.handle}`
    : `//${location.host}${cnvs.routes.root}/products/${product.handle}`

  const formattedProduct = {
    available: product.available,
    compareAtPrice: formatPrice(product.compareAtPriceRange),
    description: product.descriptionHtml,
    handle: product.handle,
    id: product.liquid ? product.id : decodeApiId(product.id),
    images: product.liquid ? product.images : formatImages(product),
    options: formatOptions(product),
    price: formatPrice(product.priceRange),
    tags: product.tags,
    title: product.title,
    type: product.productType,
    url,
    preOrder: product.preOrder?.value || false,
  }

  /**
   * Add additional values which rely on formatted response.
   */
  formattedProduct.featuredImage = formattedProduct.images[0]
  formattedProduct.giftCard = formattedProduct.type === 'Gift Cards'
  formattedProduct.variants = product.liquid ? product.variants : formatVariants(product, formattedProduct.giftCard)

  formattedProduct.hasOnlyDefaultVariant = product.hasOnlyDefaultVariant || formattedProduct.variants.every((variant) => {
    return variant.title.trim().toLowerCase() === 'default title'
  })

  return formattedProduct
}

/**
 * Formats price.
 * @param {Object} price - Price response.
 * @returns {Object}
 */
function formatPrice(price) {
  return {
    amount: (Number(price?.minVariantPrice.amount) * 100) || false,
    currencyCode: price?.minVariantPrice.currencyCode,
    max: (Number(price?.maxVariantPrice.amount) * 100) || false,
    min: (Number(price?.minVariantPrice.amount) * 100) || false,
    varies: (price?.maxVariantPrice.amount !== price?.minVariantPrice.amount),
  }
}

/**
 * Formats images.
 * @param {Object} response - GraphQL response.
 * @returns {Array}
 */
function formatImages(product) {
  return transformEdges(product.images).map((image) => {
    const linkedVariant = transformEdges(product.variants).find((variant) => {
      return variant.image.id === image.id
    })

    return {
      alt: image.altText,
      height: image.height,
      id: decodeApiId(image.id),
      src: image.url,
      variant: decodeApiId(linkedVariant?.id) || false,
      width: image.width,
    }
  })
}

/**
 * Formats options.
 * @param {Object} response - GraphQL response.
 * @returns {Array}
 */
function formatOptions(product) {
  return product.options.map((option, index) => {
    return {
      index,
      name: option.name,
      values: option.values,
    }
  })
}

/**
 * Formats variants.
 * @param {Object} response - GraphQL response.
 * @param {Boolean} isGiftCard - Product type is gift card.
 * @returns {Array}
 */
function formatVariants(product, isGiftCard) {
  return transformEdges(product.variants).map((variant) => {
    const storeLanguage = cnvs.store.language.toUpperCase()
    const sanitizedLang = storeLanguage
      ? storeLanguage.charAt(0) + cnvs.store?.language.slice(1)
      : 'En'

    const formattedVariant = {
      available: variant.available,
      compareAtPrice: {
        amount: (Number(variant.compareAtPriceV2?.amount) * 100) || false,
        currencyCode: variant.compareAtPriceV2?.currencyCode || false,
      },
      ecoTax: variant.ecoTax?.value || false,
      recommendedPrice: variant.recommendedPrice?.value || false,
      highestPrice: variant.highestPrice?.value || false,
      lowestPrice: variant.lowestPrice?.value || false,
      id: decodeApiId(variant.id),
      image: decodeApiId(variant.image?.id) || false,
      inventory: variant.quantityAvailable,
      price: {
        amount: (Number(variant.priceV2.amount) * 100),
        currencyCode: variant.priceV2.currencyCode,
      },
      shortDesc: variant[`shortDesc${sanitizedLang}`]
        ? JSON.parse(variant[`shortDesc${sanitizedLang}`]?.value)
        : false,
      selectedOptions: variant.selectedOptions.map((option) => {
        return {
          name: option.name,
          value: option.value,
        }
      }),
      sku: variant.sku,
    }

    /**
     * Want for prices to be formatted before determining title.
     * - Gift cards show the price as their title for multi-currency purposes.
     */
    formattedVariant.title = isGiftCard
      ? formatMoney(formattedVariant.price.amount, cnvs.store.moneyFormat)
      : variant.title

    return formattedVariant
  })
}

/**
 * Formats response from collection.
 * @param {Object} response - GraphQL response.
 * @returns {Object}
 */
export function formatCollection(response) {
  const collection = response.collection

  const url = cnvs.routes.root === '/'
    ? `//${location.host}/collections/${collection.handle}`
    : `//${location.host}${cnvs.routes.root}/collections/${collection.handle}`

  const formattedCollection = {
    description: collection.descriptionHtml,
    handle: collection.handle,
    id: decodeApiId(collection.id),
    title: collection.title,
    url,
  }

  /**
   * Format products.
   */
  formattedCollection.products = transformEdges(collection.products).map((productResponse) => {
    const product = productResponse

    return {
      handle: product.handle,
    }
  })

  return formattedCollection
}

/**
 * Formats response from menu.
 * @param {String} [prefix] - Prefix to prepend to handle to make it unique.
 * @param {String} [previousHandle] - Previous tier's handle.
 * @param {Object} response - GraphQL response or nested items array.
 * @param {Number} [tier] - Current tier number.
 * @returns {Array}
 */
export function formatMenu({
  prefix = 'mobile',
  previousHandle,
  response,
  tier = 1,
} = {}) {
  const menu = response?.menu?.items.length ? response.menu.items : response

  return menu.map((object, index) => {
    const handle = previousHandle
      ? `${prefix}-${previousHandle}-${convertToHandle(object.title)}-${tier}-${index}`
      : `${prefix}-${convertToHandle(object.title)}-${tier}-${index}`

    const links = object.items?.length
      ? formatMenu({
          prefix,
          previousHandle: handle,
          response: object.items,
          tier: (tier + 1),
        })
      : []

    return {
      handle,
      links,
      tier,
      title: object.title,
      url: object.url,
    }
  })
}

/**
 * Get variant image url.
 * @param {Object} variant - Variant object.
 * @param {Object} product - Product object
 * @returns {String}
 */
function getVariantImage(variant, product) {
  const imageId =  decodeApiId(variant.image?.id) || false

  if(!imageId || !product.images) {
    return false
  }

  const variantImage = transformEdges(product.images).find((image) => {
    return decodeApiId(image.id) === imageId
  })

  return variantImage.url
}

/**
 * Get a size image URL for Shopify's CDN.
 * @param {String} src - Image URL.
 * @param {String} size - Image size filter.
 * @returns {String}
 */
export function getSizedImageUrl(src, size) {
  if (size === null || size === 'master') {
    return src
  }

  const match = src.match(/\.(webp|jpg|jpeg|gif|png|bmp|bitmap|tiff|tif)(\?v=\d+)?$/i)

  if (match) {
    const prefix = src.split(match[0])
    const suffix = match[0]

    return `${prefix[0]}_${size}${suffix}`
  }

  return null
}