<template>
  <overlay
    key="redirectModal"
    block-class="redirect-modal-overlay"
    direction="up"
    namespace="redirectModal"
    type="modal"
  >
    <template #body>
      <div
        class="redirect-modal"
      >
        <div
          class="modal__overlay"
          data-micromodal-close
        >
          <div js-modal="body">
            <div
              class="modal__card redirect-modal__card"
              tabindex="0"
              js-modal="card"
              js-redirect-modal="choiceContainer"
            >
              <div class="redirect-modal__header">
                <div class="container">
                  <h5
                    class="redirect-modal__title--choices"
                    v-text="message.title.replace('%customer_location%', location.countryName)"
                  />
                  <p
                    class="redirect-modal__lead"
                    v-text="message.text.replace('%redirect_store_name%', location.redirectStoreName)"
                  />
                </div>
              </div>

              <div class="redirect-modal__body">
                <div class="container">
                  <div class="row no-margin">
                    <div class="col xs12 m6">
                      <button
                        class="choice-card"
                        data-micromodal-close
                        js-redirect-modal="choiceContinue"
                        @click="closeOverlay('redirectModal')"
                      >
                        <span class="choice-card__icon">

                          <span
                            v-if="storeCode == 'ROW'"
                            class="flag-row"
                          >
                            <globe-icon />
                          </span>
                          <span
                            v-else
                            class="flag-icon"
                            :class="`flag-icon-${ storeCode.toLowerCase()}`"
                          />
                        </span>

                        <span
                          class="choice-card__label"
                          v-text="message.continue.replace('%current_store_name%', location.currentStoreName)"
                        />
                      </button>
                    </div>

                    <div class="col xs12 m6">
                      <a
                        class="choice-card"
                        :href="location.redirectStoreUrl"
                        rel="nofollow"
                        :data-current-store-code="storeCode.toLowerCase()"
                        data-redirect-store-code="%redirect_store_code%"
                        js-redirect-modal="redirectLink"
                      >
                        <span class="choice-card__icon">
                          <span
                            class="flag-icon"
                            :class="`flag-icon-${location.redirectStoreFlag}`"
                          />
                          <span
                            class="is-hidden"
                            :class="`flag-${location.redirectStoreFlag}` "
                          >
                            <globe-icon />
                          </span>
                        </span>

                        <span
                          class="choice-card__label"
                          v-text="message.redirect.replace('%redirect_store_name%', location.redirectStoreName)"
                        />
                      </a>
                    </div>
                  </div>
                </div>
              </div>

              <div class="redirect-modal__footer">
                <div class="container">
                  <button
                    class="text-link"
                    @click="toggleStoreSelector"
                    v-text="$string('redirect.modal.settings.label')"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </overlay>
</template>

<script>

/**
 * Vue: redirect-modal (redirect-modal)
 * -----------------------------------------------------------------------------
 * Redirect modal.
 *
 */
import { mapActions } from 'vuex'
import { stores } from '~/helpers/multi-store'
import Cookies from 'js-cookie'

import CloseIcon from '~icons/misc/close.svg'
import GlobeIcon from '~icons/misc/globe.svg'

export default {
  name: 'RedirectModal',

  components: {
    CloseIcon,
    GlobeIcon,
  },

  data() {
    return {
      global: true,
      cookieSelector: {
        geolocation: 'redirect_geolocation',
        settings: 'redirect_store_settings',
      },
      location: {
        currentStoreName: '%current_store_name%',
        countryName: '%customer_location%',
        redirectStoreCode: '%redirect_store_code%',
        redirectStoreFlag: '%redirect_store_flag%',
        redirectStoreName: '%redirect_store_name%',
        redirectStoreUrl: '%redirect_store_url%',
      },
      message: {
        continue: this.$string('redirect.modal.choice.continue'),
        redirect: this.$string('redirect.modal.choice.redirect'),
        text: this.$string('redirect.modal.choice.text'),
        title: this.$string('redirect.modal.choice.title'),
      },
      storeCode: window.cnvs.store.storeCode,
    }
  },

  computed: {

    /**
     * Get geolocation cookies.
     * @returns {Object}
     */
    getGeolocationCookie() {
      const geolocationCookies = Cookies.get(this.cookieSelector.geolocation)
      return geolocationCookies ? JSON.parse(geolocationCookies) : geolocationCookies
    },
  },

  mounted() {
    this.fetchUserGeolocation()
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      openOverlay: 'overlays/open',
      closeOverlay: 'overlays/close',
    }),

    /**
     * Return Geolocation data of current user.
     * - Check theme settings of geolocation service.
     * - Make request to MaxMind and resolve formatted data.
     */
    fetchUserGeolocation() {
      return new Promise((resolve, reject) => {

        /**
         * Use cached geolocation data if it has already been requested.
         */
        if (typeof this.getGeolocationCookie !== 'undefined') {
          resolve(this.getGeolocationCookie)
          return
        }

        /**
         * Make a request to MaxMind and cache in a cookie.
         */
        const onSuccess = (response) => {
          const formattedData = this.getFormattedGeolocationData(response)
          resolve(formattedData)
          return formattedData
        }

        const onError = (error) => {
          reject(error)
        }

        geoip2.country(onSuccess, onError)
      }).then((response) => {
        if (this.isMatchedLocation(response) || this.getGeolocationCookie) {
          return
        }

        this.setRedirectModal(response)
      })
    },

    /**
     * Format user geolocation information from response.
     * @param {Object} response - maxmind successful response.
     * @returns {Object}
     */
    getFormattedGeolocationData(response) {
      return {
        continentCode: response.continent.code,
        continentName: response.continent.names.en,
        countryCode: response.country.iso_code,
        countryName: response.country.names.en,
      }
    },

     /**
     * Get the matching store based on the user's location.
     * - Will check countries then supported stores, then defaulting to continent.
     * @param {Object} response - Formatted store and redirect country codes from settings.
     * @returns {Object}
     */
    getMatchingStore(countryCode) {
      if (!countryCode) {
        return
      }

      const matchingCountry = stores.find((store) => store.storeCode.toLowerCase() == countryCode.toLowerCase())
      const matchingSupportedStores = stores.filter((store) => store.supportedStores.includes(countryCode))

      if (!matchingCountry) {
        return matchingSupportedStores[0] || false
      }

      return matchingCountry
    },

    /**
     * Check if the users geolocation matches the store's location or supported locations.
     * @param {Object} response - GeoIP response from maxmind.
     * @returns {Boolean}
     */
    isMatchedLocation(response) {
      if (!response?.countryCode) {
        return
      }

      const matchingStore = this.getMatchingStore(response.countryCode)
      return (matchingStore.storeCode.toLowerCase() === this.storeCode.toLowerCase())
    },

    /**
     * Sets and displays the redirect modal
     * @param {Object} response - maxmind successful response.
     */
    setRedirectModal(response) {
      this.updateLocation(response)
      this.setGeolocationCookie(response)

      this.openOverlay({
        ignoreDismissed: true,
        namespace: 'redirectModal',
      })
    },

    /**
     * Set current customer and store location.
     * @param {Object} location - maxmind successful response.
     */
    updateLocation(location) {
      const matchingStore = this.getMatchingStore(location.countryCode)

      this.location = {
        countryName:location.countryName,
        currentStoreName: cnvs.store.countryName,
        redirectStoreName: matchingStore.storeName,
        redirectStoreUrl: matchingStore.storeUrl,
        redirectStoreFlag: matchingStore.storeCode.toLowerCase(),
      }
    },

    /**
     * Set cookie events based on request.
     * @param {Object} response - maxmind successful response.
     */
    setGeolocationCookie(response) {
      Cookies.set(this.cookieSelector.geolocation, JSON.stringify(response), { expires: 7 })
    },


    /**
     * Toggle the store selector modal.
     */
    toggleStoreSelector() {
      this.openOverlay({
        ignoreDismissed: true,
        namespace: 'storeSwitcher',
      })

    },
  },
}
</script>
