<template>
  <ais-instant-search
    class="search-bar"
    :class="{'has-results': showResults}"
    :index-name="getIndex('products_query_suggestions')"
    :search-client="searchClient"
  >
    <ais-configure
      :hits-per-page.camel="4"
    />
    <ais-index :index-name="getIndex('collections')" />
    <ais-index :index-name="getIndex('pages')" />
    <ais-index :index-name="getIndex('products')" />

    <ais-autocomplete>
      <template #default="{ currentRefinement, indices, refine }">
        <form
          class="search-bar__form"
          @submit.prevent="onSubmit"
        >
          <div class="search-bar__form-wrap">
            <input
              ref="searchInput"
              v-model="query"
              autocomplete="off"
              class="search-bar__input"
              name="q"
              type="text"
              :placeholder="$string('search.placeholder')"
              @input="refineSearch($event.currentTarget.value, refine)"
              @change="refineSearch($event.currentTarget.value, refine)"
            >
            <button
              class="search-bar__submit"
              type="submit"
              :aria-label="$string('search.submit')"
            >
              <icon-search />
            </button>
          </div>
        </form>

        <search-bar-results
          v-if="predictive"
          ref="searchResults"
          :current-refinement="currentRefinement"
          :indices="indices"
          :search="{
            input: $refs.searchInput
          }"
          :query="query"
        />
      </template>
    </ais-autocomplete>
  </ais-instant-search>
</template>

<script>

/**
 * Vue: Search bar (search-bar)
 * -----------------------------------------------------------------------------
 * Search Bar with Autocomplete powered by Algolia.
 *
 */
import {
  AisAutocomplete,
  AisConfigure,
  AisIndex,
  AisInstantSearch,
} from 'vue-instantsearch/vue3/es'
import algoliasearch from 'algoliasearch/lite'

import SearchBarResults from '~global/search-bar/search-bar-results'

import IconSearch from '~icons/misc/search.svg'

export default {
  name: 'SearchBar',

  components: {
    AisAutocomplete,
    AisConfigure,
    AisIndex,
    AisInstantSearch,
    SearchBarResults,
    IconSearch,
  },

  props: {
    predictive: {
      type: Boolean,
      default: true,
    },
    defaultValue: {
      type: String,
      default: '',
    },
  },

  emits: ['query-update'],

  data() {
    return {
      searchClient: {},
      query: '',
      showResults: false,
    }
  },

  mounted() {
    this.setDefaultValue()

    cnvs.EventBus.listen('search:open', (state) => {
      this.$nextTick(() => {
        if (this.$refs.searchInput && state) {
          this.$refs.searchInput.focus()
        }
      })
    })
  },

  created() {
    const algoliaClient = algoliasearch(
      window.algoliaShopify.config.app_id,
      window.algoliaShopify.config.search_api_key,
    )

    this.searchClient = {
      ...algoliaClient,
      search(requests) {
        if (requests.every(({ params }) => !params.query)) {
          return Promise.resolve({
            results: requests.map(() => ({
              hits: [],
              nbHits: 0,
              processingTimeMS: 0,
            })),
          })
        }

        return algoliaClient.search(requests)
      },
    }
  },

  methods: {

    /**
     * Refine Search
     */
    refineSearch(val, refine) {
      this.showResults = this.query.length ? true : false
      refine(val, refine)
    },

    /**
     * Get prefixed index.
     */
    getIndex(name) {
      return `${window.algoliaShopify.config.index_prefix}${name}`
    },

    /**
     * On form submit.
     */
    onSubmit() {
      if (!this.query.length) {
        return
      }

      if (!this.predictive) {
        this.$emit('query-update', this.query)
        return
      }

      window.location.href = `${this.$variable('routes.search')}?q=${encodeURIComponent(this.query)}`
    },

    /**
     * Set default search bar value if one is specified.
     */
    setDefaultValue() {
      if (this.defaultValue) {
        this.query = this.defaultValue
      }
    },
  },
}
</script>

<style lang="scss">
@import './search-bar';
</style>
