<template>
  <ul
    class="store-selector__tools"
    :class="{
      [`${customClass}__tools`]: customClass,
    }"
  >
    <li
      class="store-selector__tool-item"
      :class="{
        [`${customClass}__tool-item`]: customClass,
      }"
    >
      <button
        class="store-selector__link subtitle-2"
        :class="{
          [`${customClass}__link`]: customClass,
        }"
        :aria-label="$string('accessibility.country_selector.label')"
        @click="openSwitcher()"
        v-text="$string('general.store_switcher.default_country')"
      />
    </li>
    <li
      class="store-selector__tool-item"
      :class="{
        [`${customClass}__tool-item`]: customClass,
      }"
    >
      <button
        class="store-selector__link subtitle-2"
        :class="{
          [`${customClass}__link`]: customClass,
        }"
        :aria-label="$string('accessibility.country_selector.label')"
        @click="openSwitcher()"
        v-text="defaultLanguage || $string('general.store_switcher.default_lang')"
      />
    </li>
  </ul>
</template>


<script>
import { mapActions } from 'vuex'

export default {
  name: 'StoreSelector',
  props: {
    customClass: {
      type: [Boolean, String],
      default: false,
    },
  },
  data() {
    return {
      defaultLanguage: cnvs.store.language.toUpperCase(),
    }
  },
  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      openOverlay: 'overlays/open',
    }),

    openSwitcher() {
      this.openOverlay({
        ignoreDismissed: true,
        namespace: 'storeSwitcher',
      })
    },
  },
}
</script>