<template>
  <div
    v-if="colorSwatchIndex >= 0 && !isAlgolia"
    class="product-card__swatches product-card-swatches"
  >
    <button
      v-for="(variant, index) in product.variants"
      ref="swatches"
      :key="index"
      class="product-card-swatches__swatch"
      :class="{ [$class('active')]: selectedColor === variant.options[colorSwatchIndex]}"
      :title="variant.options[colorSwatchIndex]"
      type="button"
      :data-href="`${product.handle}?variant-color=${encodeURIComponent(variant.options[colorSwatchIndex])}`"
      :data-value="variant.options[colorSwatchIndex]"
      :data-variant-image="variant.featured_image?.src || variant.featured_image"
      :data-variant-id="variant.id"
      :data-variant-price="variant.price"
      :data-variant-compare-price="variant.compare_at_price"
      @click.prevent.stop="handleSwatchClick"
    >
      <img
        :alt="variant.options[colorSwatchIndex]"
        class="product-card-swatches__image"
        :src="sanitizedSwatchUrl(variant.options[colorSwatchIndex])"
      >
    </button>
  </div>

  <div
    v-else-if="algoliaSwatches.length > 0"
    class="product-card__swatches product-card-swatches"
  >
    <button
      v-for="(color, index) in algoliaSwatches"
      ref="swatches"
      :key="index"
      class="product-card-swatches__swatch"
      :title="setProductTitle()"
      type="button"
      :data-href="`${product.handle}?variant-color=${encodeURIComponent(color)}`"
      :data-value="color.color"
      :class="{ [$class('active')]: selectedColor.color === color.color}"
      @click.prevent.stop="() => handleAlgoliaSwatchClick(color)"
    >
      <img
        :alt="color.color"
        class="product-card-swatches__image"
        :src="sanitizedSwatchUrl(color)"
      >
    </button>
  </div>

  <!-- Include option if bundles card -->
</template>

<script>

/**
 * Vue: Product card swatches (product-card-swatches)
 * -----------------------------------------------------------------------------
 * Product card swatches.
 *
 */
import product from '~/graphql/queries/product.gql'
import { formatProduct } from '~/helpers/format'

export default {
  name: 'ProductCardSwatches',

  props: {
    isAlgolia: {
      type: Boolean,
      default: false,
    },
    collection: {
      type: [Boolean, Object],
      default: false,
    },
    product: {
      type: [Boolean, Object],
      default: false,
      required: true,
    },
  },

  emits: ['update-swatch'],

  data() {
    return {
      algoliaSwatches: [],
      selectedColor: '',
      storeLang: cnvs.store.storeLang,
    }
  },

  computed: {

    /**
     * Compute color swatch index
     * @returns {Number}
     */
    colorSwatchIndex() {
      if (this.isAlgolia || !this.product.options) {
        return -1
      }

      const index = this.product.options?.indexOf('Color') || this.product.options?.indexOf('Colour')

      return index
    },
  },

  mounted() {
    if (this.isAlgolia) {
      this.setInitialCollectionSwatches()
      return
    }

    this.setInitialSwatch()
  },

  methods: {

    getDefaultImage(variant) {
      return variant.featured_image?.src
        || this.product.featured_image
        || this.product.image
    },

    /**
     * Handle swatch click from product card
     * @param {Object} event - Click event
     */
    handleSwatchClick(event) {
      const swatch = event.currentTarget

      this.selectedColor = swatch.dataset.value

      this.$emit('update-swatch', {
        target: swatch,
        ...swatch.dataset,
      })
    },

    /**
     * Handle swatch click from product card with Algolia data
     * @param {String} color - Selected color.
     */
    handleAlgoliaSwatchClick(color) {
      if(this.algoliaSwatches <= 1) {
        return
      }

      this.selectedColor = color
      this.queryProduct(this.product.handle)
    },

    /**
     * Loads product from GraphQL.
     * @param {String} [handle] - Product handle to load.
     */
    async queryProduct(handle = this.handle) {
      if (handle === false) {
        return
      }

      try {
        const response = await cnvs.Query({
          locale: cnvs.store.language,
          query: product,
          variables: {
            country: cnvs.store.country,
            handle,
          },
        })

        if (cnvs.settings.disableComponentReadyState) {
          return
        }

        this.updateSwatches(formatProduct(response))

      } catch (error) {
        reportError('Failed to load product', error)
      }
    },

    /**
     * Update swatches
     * @param {Object} product -Current product data.
     */
    updateSwatches(product) {
      const variant = product.variants.find((variant) => {
        return variant.selectedOptions.every((option) => {
          const optionName = option.name.toLowerCase()
          const optionValue = option.value.toLowerCase()
          const selectedColorValue = this.selectedColor[optionName].toLowerCase()

          return selectedColorValue === optionValue
        })
      })

      const image = product.images.find((image) => {
        return image.id === variant.image
      })

      const compareAtPrice = variant.compare_at_price || variant.compareAtPrice

      this.$emit('update-swatch', {
        variantId: variant.id,
        variantImage: image.src,
        variantPrice: variant.price.amount,
        variantComparePrice: compareAtPrice.amount || variant.price.amount,
        recommendedPrice: compareAtPrice.amount > variant.price.amount ? compareAtPrice.amount  : false,
        lowestPrice: variant.lowestPrice,
        ...variant,
        title: `${product.title} (${variant.title})`,
      })
    },

    /**
     * Sanitized swatch url
     * @param {String} color - Swatch color.
     * @return {String}
     */
    sanitizedSwatchUrl(color) {
      return cnvs.urls.files
        .replace('placeholder', `swatch_${color.color.toLowerCase()}_20x20.png`)
    },

    /**
     * Set algolia swatches on collection pages.
     */
    setInitialCollectionSwatches() {
      const lowestPrice = this.product.meta.global?.lowest_price.replace(/_DELETE_/g, '') || false
      const colors = this.product.named_tags?.color || []
      const variantData = {
        variantId: this.product.objectID,
        variantImage: this.product.image,
        variantPrice: this.product.price,
        variantComparePrice: this.product.compare_at_price,
        lowestPrice,
      }

      this.algoliaSwatches = typeof colors === 'string' ? [
        {
          ...this.product.options,
          color: colors,
        },
      ] : colors.map(color => ({ ...this.product.options, color }))

      if (this.product.options.color) {
        this.selectedColor = {
          ...this.product.options,
          color: this.product.options.color,
        }
        variantData.target = this.product.options.color
      }

      this.$emit('update-swatch', variantData)
    },

    setProductTitle() {
      const { meta } = this.product
      const metaProductTitle = meta[`${this.storeLang}_product`] || meta.en_product
      const productTitle = `${this.product.title} (${this.product.variant_title})`

      const title = (!meta
        ? productTitle
        : `${metaProductTitle ? `${metaProductTitle.title}` : productTitle}`
      )

      return title
    },

    /**
     * Set the initial swatch
     */
    setInitialSwatch() {
      const { swatches } = this.$refs
      const variant = this.product.variants[0]
      const image = this.getDefaultImage(variant)

      if (!swatches || !swatches.length) {
        this.$emit('update-swatch', {
          variantId: variant.id,
          variantImage: image,
          variantPrice: variant.price,
          variantComparePrice: variant.compare_at_price,
        })
        return
      }

      const swatch = swatches[0]
       const selectedSwatch = {
        target: swatch,
        ...swatch.dataset,
      }

      if (!selectedSwatch.variantImage) {
        selectedSwatch.variantImage = image
      }

      this.selectedColor = swatch.dataset.value

      this.$emit('update-swatch', selectedSwatch)
    },
  },
}
</script>

<style lang="scss">
@import './product-card-swatches';
</style>
