/**
 * Layout: Theme
 * -----------------------------------------------------------------------------
 * Entry file for all global theme scripts.
 *
 */
import '@/layout/theme.scss'

/**
 * Core scripts.
 */
import '~/core/lazysizes'
import AppHandler from '~/core/app-handler'
import Bugsnag from '~/core/bugsnag'
import Canvas from '~/core/canvas'
import Commands from '~/core/commands'
import EventBus from '~/core/event-bus'
import Query from '~/core/query'
import { initAlgoliaTracking, trackConversion } from '~/helpers/algolia'
import { on } from '~/helpers/general'

/**
 * Global Canvas functions.
 */
window.cnvs = window.cnvs || {}
window.cnvs.EventBus = EventBus()
window.cnvs.Helpers = Commands().cli()
window.cnvs.Query = (query) => Query(query)

/**
 * Open genesys chat widget
 * https://developer.genesys.cloud/commdigital/digital/webmessaging/messengersdk/SDKCommandsEvents/messengerPlugin
 */
function toggleMessenger() {
  // eslint-disable-next-line no-undef
  Genesys('command', 'Messenger.open', {},
    // eslint-disable-next-line no-empty-function
    () => {},
    () => {
      // eslint-disable-next-line no-undef
      Genesys('command', 'Messenger.close');
    });
}

document.addEventListener('DOMContentLoaded', () => {
  initAlgoliaTracking()
  Bugsnag().init()
  Commands().init()
  AppHandler().beforeVueInit()
  Canvas().init()
  AppHandler().afterVueInit()
  trackConversion()

  const links = document.links

  for (let i = 0, linksLength = links.length; i < linksLength; i++) {
    const link = links[i]
    if (link.href.includes('#chatbot')) {
      on('click', link, (event) => {
        event.preventDefault()
        event.stopPropagation()

        toggleMessenger()
      })
    }
  }
})
