<template>
  <div
    class="image-banner critical-component-clear"
    :class="imageAlignment"
  >
    <div class="container">
      <div class="image-banner__container">
        <component
          :is="imageElement"
          class="image-banner__banner"
          :href="ctaLink"
          :aria-label="ctaAriaLabel.length ? ctaAriaLabel : ctaText"
        >
          <div class="image-banner__primary-image-wrapper">
            <div
              class="image-banner__primary-image lazyload"
              :data-bgset="backgroundImage"
            />
          </div>

          <div
            v-if="overlayImage && overlayImage.src"
            class="image-banner__overlay-image-wrapper"
            js-image-banner="overlay"
          >
            <div class="image-banner__overlay-image">
              <responsive-image
                :image="overlayImage"
                min-max="550-550"
              />
            </div>
          </div>
        </component>

        <div class="image-banner__content">
          <div
            class="image-banner__text"
            :class="textAlignment"
          >
            <p
              v-if="subtitleText"
              class="image-banner__subtitle subtitle-2"
            >
              <text-with-icon :text="subtitleText" />
            </p>

            <h2
              v-if="headingText"
              class="image-banner__heading h3"
            >
              {{ headingText }}
            </h2>

            <div
              v-if="desc"
              class="image-banner__desc h6"
            >
              {{ desc }}
            </div>

            <a
              v-if="ctaLink && ctaText"
              class="image-banner__cta button"
              :class="[`button--${ctaTheme}`]"
              :href="ctaLink"
              :aria-label="ctaAriaLabel.length ? ctaAriaLabel : ctaText"
            >
              {{ ctaText }}
              <icon-arrow-up-right />
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/all'
import { on, debounce } from '~/helpers/general'
import ResponsiveImage from '~global/images/responsive-image'
import TextWithIcon from '~global/text-with-icon/text-with-icon'
import IconArrowUpRight from '~icons/direction/arrow/up-right.svg'

/**
 * Vue: Image Banner Block (image-banner-block)
 * -----------------------------------------------------------------------------
 * Image Banner Block.
 *
 * @param {String} imageAlignment - Image alignment setting.
 * @param {String} backgroundImage - Background image. Must be rendered with
 * responsive-bg-image snippet
 * @param {Object} overlayImage - Overlay image.
 * @param {String} textAlignment - Text alignment setting.
 * @param {String} subtitleText - Subtitle text.
 * @param {String} headingText - Heading text.
 * @param {String} desc - Description text.
 * @param {String} ctaLink - CTA link.
 * @param {String} ctaText - CTA text.
 * @param {String} ctaTheme - CTA theme.
 */
export default {
  name: 'ImageBannerBlock',

  components: {
    ResponsiveImage,
    TextWithIcon,
    IconArrowUpRight,
  },

  props: {
    imageAlignment: {
      type: String,
      default: '',
    },
    backgroundImage: {
      type: String,
      default: '',
    },
    overlayImage: {
      type: Object,
      default: () => ({}),
    },
    textAlignment: {
      type: String,
      default: '',
    },
    subtitleText: {
      type: String,
      default: '',
    },
    headingText: {
      type: String,
      default: '',
    },
    desc: {
      type: String,
      default: '',
    },
    ctaLink: {
      type: String,
      default: '',
    },
    ctaText: {
      type: String,
      default: '',
    },
    ctaAriaLabel: {
      type: String,
      default: '',
    },
    ctaTheme: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      selectors: {
        overlayImage: '[js-image-banner="overlay"]',
      },
      nodeSelectors: {},
      instances: [],
      screenSize: '',
      global: true,
    }
  },

  computed: {
    imageElement() {
      return this.ctaLink ? 'a' : 'div'
    },
  },

  mounted() {
    gsap.registerPlugin(ScrollTrigger)
    const overlayImage = this.$el.querySelector(this.selectors.overlayImage)
    this.screenSize = this.getScreenSize()

    if (!overlayImage) {
      return
    }

    this.addAnimation(overlayImage)
    this.setEventListeners()
  },

  methods: {
    getScreenSize() {
      if (window.matchMedia('(min-width: 768px)').matches) {
        return 'medium'
      } else {
        return 'small'
      }
    },

    addAnimation(overlayImage, instance = {}) {
      instance.overlayImage = overlayImage
      instance.container = this.$el

      /**
       * Create a timeline with a scrollTrigger so the animation is tied
       * to the scroll progress.
       */
      instance.timeline = gsap.timeline({
        scrollTrigger: {
          trigger: instance.container,
          start: 'top bottom',
          end: 'bottom top',
          scrub: 0.2,
        },
      })

      let animationStart = 50
      let animationEnd = -75

      // Increase intensity of animation on bigger screens
      if (this.screenSize === 'medium') {
        animationStart = 300
        animationEnd = -150
      }

      // Add animation to timeline
      instance.timeline.fromTo(
        instance.overlayImage,
        1,
        { y: animationStart },
        { y: animationEnd, ease: 'power1.easeInOut' },
      )


      // If progress is tracked, start from that point
      if (instance.progress) {
        instance.timeline.progress(instance.progress)
      }

      return instance
    },

    setEventListeners() {
      on('resize', debounce(() => this.handleResizeEvent(), 250))
    },

    handleResizeEvent() {
      if (this.screenSize === this.getScreenSize()) {
        return
      }

      this.screenSize = this.getScreenSize()
      this.killInstances()
      this.reInitInstances()
    },

    killInstances() {
      this.instances.forEach((instance) => {
        // Save progress before reinit
        instance.progress = instance.timeline.progress()

        // Kill GSAP timeline
        instance.timeline.kill()
      })
    },

    reInitInstances() {
      this.instances.forEach((instance) => {
        this.addAnimation(instance.overlayImage, instance)
      })
    },
  },
}
</script>

<style lang="scss">
@import './image-banner-block';
</style>
