<template>
  <nav
    class="breadcrumb body-2"
    :class="$blockClass(blockClass, 'breadcrumbs')"
    role="navigation"
    aria-label="breadcrumb"
  >
    <a
      class="breadcrumb__item breadcrumb__link"
      :class="$blockClass(blockClass, 'breadcrumbs__list-item')"
      :href="storeUrl"
      title="Home"
      v-text="$string('general.breadcrumb.home')"
    />

    <span
      class="breadcrumb__separator"
      v-text="breadcrumbSeparator"
    />

    <template v-if="additionalPath">
      <a
        class="breadcrumb__item breadcrumb__link"
        :href="additionalPath.url"
        v-text="additionalPath.title"
      />

      <span
        class="breadcrumb__separator"
        v-text="breadcrumbSeparator"
      />
    </template>

    <span
      :class="$blockClass(blockClass, 'breadcrumbs__link')"
      v-text="pageTitle"
    />
  </nav>
</template>

<script>

/**
 * Vue: breadcrumb (breadcrumb)
 * -----------------------------------------------------------------------------
 * breadcrumb component shared between PDP and collections.
 *
 * @param {Boolean|Object} additionalPath - Additional breadcrumbs path before current title.
 * @param {String} blockClass - Block level class to apply to all elements.
 * @param {String} breadcrumbSeparator - Separator used between breadcrumb paths.
 * @param {String} pageTitle - Current page title.
 *
 */
export default {
  name: 'Breadcrumb',

  props: {
    additionalPath: {
      type: [Boolean, Object],
      default: false,
    },
    blockClass: {
      type: String,
      default: '',
    },
    breadcrumbSeparator: {
      type: String,
      default: '/',
    },
    pageTitle: {
      type: String,
      default: '',
      required: true,
    },
  },

  data() {
    return {
      storeUrl: `https://${cnvs.store.shopUrl}`,
    }
  },
}
</script>

<style lang="scss">
  @import './breadcrumb';
</style>