<template>
  <a
    class="product-card"
    data-cy="productCard"
    :class="{'product-card--alt': enabledImageHover, 'product-card__bundle': hasBundle}"
    :href="productUrl(product.handle)"
    :aria-label="productTitle"
    :target="hasBundle ? '_blank' : '' "
  >
    <div class="product-card__thumbnail-container">
      <responsive-image
        v-if="hasImage"
        class="responsive-card-image__wrapper product-card__thumbnail product-card__thumbnail--primary"
        block-class="product-card"
        :force-update="true"
        min-max="200-500"
        ratio="3:4"
        :padding="false"
        :image="{ url: selectedSwatch.variantImage }"
        :lazyload="true"
      />
      <img
        v-else
        :src="placeholderImage"
        tabindex="-1"
      >
    </div>

    <div class="product-card__footer">
      <span
        class="product-card__title subtitle-1"
        v-text="title"
      />

      <product-price-message
        v-if="hasPricingMessage"
        :lowest-price="lowestPrice"
        :recommended-price="recommendedPrice"
      />

      <product-prices
        :class="$blockClass(blockClass, 'product-card-prices')"
        :available="product?.available"
        block-class="product-card"
        :compare-at-price="getCompareAtPrice"
        :price="hasBundle ? bundlePrice : productPrice"
        :show-original-price="showOriginalPrice"
        :style="{ animationDelay: animationDelay }"
        :compareAtPriceVal="compareAtPriceValue"
      />
    </div>

    <div class="product-card__extras">
      <product-card-reviews
        :product="product"
      />
      <!-- Render product swatches -->
      <product-card-swatches
        :collection="collection"
        :product="product"
        :is-algolia="isAlgolia"
        @update-swatch="updateCardImage"
      />
    </div>
  </a>
</template>

<script>

/**
 * Vue: Product card
 * -----------------------------------------------------------------------------
 * Product card used throughout the site.
 *
 * @param {String} [blockClass] - Block level class to apply to all elements.
 * @param {Number} index - Product card's forloop index.
 * @param {Boolean|Object} product - Product object, false shows loading state.
 *
 */

import ProductCardSwatches from '~async/product-card-swatches/product-card-swatches'
import ResponsiveImage from '~global/images/responsive-image'
import ProductPriceMessage from '~global/product-price-message/product-price-message'
import ProductCardReviews from '~async/product-card/product-card-reviews'
import ProductPrices from '~global/product-prices/product-prices'

import { values as timings } from '~/config/timings'
// import { type } from 'src/schemas/fragments/colour'

export default {
  name: 'ProductCard',

  components: {
    ProductCardSwatches,
    ProductPrices,
    ProductPriceMessage,
    ResponsiveImage,
    ProductCardReviews,
  },

  props: {
    hasBundle: {
      type: Boolean,
      default: false,
    },
    blockClass: {
      type: String,
      default: '',
    },
    index: {
      type: Number,
      default: 0,
    },
    collection: {
      type: [Boolean, Object],
      default: false,
    },
    isAlgolia: {
      type: Boolean,
      default: false,
    },
    product: {
      type: [Boolean, Object],
      default: false,
      required: true,
    },
    discountAmount: {
      type: [Number, Boolean],
      default: false,
    },
    showOriginalPrice: {
      type: Boolean,
      default: false,
    },
    discountAmountSaved: {
      type: [Number, Boolean],
      default: false,
    },
    discountType: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      enabledImageHover: cnvs.productCards.enableAltProductImageHover,
      imageKey: 1,
      selectedSwatch: false,
      storeLang: cnvs.store.storeLang,
      title: '',
    }
  },

  computed: {

    /**
     * Compute animation delay.
     * @returns {String}
     */
    animationDelay() {
      return `${this.index * timings.quick}s`
    },

    /**
     * Get product compare at price
     * @returns {Number}
     */
    getCompareAtPrice() {
      if (this.hasBundle) {
        return this.selectedSwatch?.price?.amount || this.product.price
      }

      return this.selectedSwatch?.compareAtPrice?.amount || this.product?.compareAtPrice || this.product?.compare_at_price || false
    },

    /**
     * Get product product bundle price
     * @returns {Number}
     */
    bundlePrice() {
      if (!this.hasBundle || !this.discountAmount || !this.discountAmountSaved) {
        return
      }

      if (this.discountType === 'amount') {
        const price = (this.product?.compare_at_price === null ? this.product?.price : this.product?.compare_at_price) - (this.discountAmountSaved * 100)
        return price
      }

      else {
        const price = (this.product?.compare_at_price === null ? this.product?.price : this.product?.compare_at_price) * (Math.abs(this.discountAmount - 100) / 100)
        return Math.round(price)
      }
    },

    compareAtPriceValue() {
      return this.product.compare_at_price === null ? this.product.price : this.product.compare_at_price
    },

    /**
     * Compute if product has any images.
     * @returns {boolean}
     */
    hasImage() {
      if (!this.product) {
        return false
      }

      return !!(this.product.image ||
        this.product.featured_image ||
        this.selectedSwatch.variantImage)
    },

    /**
     * Get placeholder image
     * @returns {String}
     */
    placeholderImage() {
      return cnvs.urls.files
        .replace('placeholder', 'image_placeholder.svg')
    },

    /**
     * Set product title based on store language
     * @returns {String}
     */
    productTitle() {
      return this.updateProductTitle()
    },

    /**
     * Get product lowest price.
     * @returns {Number}
     */
    lowestPrice() {
      if (!this.selectedSwatch?.lowestPrice) {
        return false
      }

      const sanitizedLowestPrice = this.selectedSwatch.lowestPrice
        .replace(/_DELETE_/g, '')

      return sanitizedLowestPrice.length > 0
        ? sanitizedLowestPrice
        : false
    },

    /**
     * Get product recommended price.
     * @returns {Number}
     */
    recommendedPrice() {
      const { selectedSwatch } = this
      return selectedSwatch.variantComparePrice > selectedSwatch.variantPrice
        ? this.$formatMoney(selectedSwatch.variantComparePrice)
        : false
    },

    hasPricingMessage() {
      const enabled = this.$variable('settings.pricingDirective.enable')

      if (!enabled) {
        return false
      }

      return this.selectedSwatch.variantComparePrice > this.selectedSwatch.variantPrice || this.lowestPrice
    },

    productPrice() {
      return this.selectedSwatch?.price?.amount || this.product?.price || false
    },
  },

  methods: {

    /**
     * Get product url collection handle.
     * @param {String} handle - product handle.
     *
     */
    productUrl(handle) {
      const variantId = this.selectedSwatch.variantId || this.product.objectID

      return this.collection.handle
        ? `/collections/${this.collection.handle}/products/${handle}?variant=${variantId}`
        : `/products/${handle}?variant=${variantId}`
    },

    /**
     * Update product card primary image.
     * @param {Object} swatch - Payload with swatch element and dataset.
     */
    updateCardImage(swatch) {
      this.selectedSwatch = swatch

      if (this.selectedSwatch.title) {
        this.title = this.selectedSwatch.title
      }
    },

    updateProductTitle() {
      if (!this.product) {
        return
      }

      const { meta } = this.product
      const metaProductTitle = meta ? (meta[`${this.storeLang}_product`] || meta.en_product) : false
      const productTitle = `${this.product.title} (${this.product.variant_title})`

      const title = (!meta
        ? productTitle
        : `${metaProductTitle ? metaProductTitle.title : productTitle}`
      )

      this.title = title
      return title
    },
  },

}
</script>

<style lang="scss">
@import './product-card';
</style>
