<template>
  <div
    class="accordion"
  >
    <template
      v-for="(item, index) in items"
      :key="`${section}-${index}`"
    >
      <div
        v-if="containerSizeClasses"
        :class="containerSizeClasses"
      >
        <div
          :ref="`${ section }-${ index }`"
          class="accordion__item"
          :class="{'is-active': activeItems.includes(`${ section }-${ index }`)}"
        >
          <div
            :class="`accordion__title ${titleClass}`"
            :aria-controls="`content-${ index }`"
            :aria-expanded="activeItems.includes(`${ section }-${ index }`)"
            @click.prevent="() => toggleItem(`${ section }-${ index }`)"
          >
            <span
              :aria-controls="`content-${ index }`"
              aria-expanded="false"
              v-text="item.title"
            />
            <button
              :id="`Accordion${ index }`"
              :class="buttonClass"
              :aria-controls="`content-${ index }`"
              aria-expanded="false"
              :aria-label="item.title"
            >
              <icon-minus />
              <icon-plus />
            </button>
          </div>

          <accordion-item
            :is-active="activeItems.includes(`${ section }-${ index }`)"
            :item="item"
            :index="index"
            :expand-all="expandAll"
          >
            <slot :item="item" />
          </accordion-item>
        </div>
      </div>

      <div
        v-else
        :ref="`${ section }-${ index }`"
        class="accordion__item"
        :class="{'is-active': activeItems.includes(`${ section }-${ index }`)}"
      >
        <div
          :class="`accordion__title ${titleClass}`"
          :aria-controls="`content-${ index }`"
          :aria-expanded="activeItems.includes(`${ section }-${ index }`)"
          @click.prevent="() => toggleItem(`${ section }-${ index }`)"
        >
          <span
            :aria-controls="`content-${ index }`"
            aria-expanded="false"
            v-text="item.title"
          />
          <button
            :id="`Accordion${ index }`"
            :class="buttonClass"
            class="accordion-footer__control"
            :aria-controls="`content-${ index }`"
            aria-expanded="false"
            :aria-label="item.title"
          >
            <icon-minus />
            <icon-plus />
          </button>
        </div>

        <accordion-item
          :is-active="activeItems.includes(`${ section }-${ index }`)"
          :item="item"
          :index="index"
          :expand-all="expandAll"
        >
          <slot :item="item" />
        </accordion-item>
      </div>
    </template>

    <slot />
  </div>
</template>

<script>

/**
 * Vue: accordion (accordion)
 * -----------------------------------------------------------------------------
 * Accordion.
 *
 */
import IconMinus from '~icons/misc/minus.svg'
import IconPlus from '~icons/misc/plus.svg'
import AccordionItem from '~global/accordion/accordion-item'
import breakpoints from '~/config/breakpoints'

export default {
  name: 'Accordion',

  components: {
    AccordionItem,
    IconMinus,
    IconPlus,
  },

  props: {
    buttonClass: {
      type: String,
      default: '',
    },
    containerSizeClasses: {
      type: String,
      default: '',
    },
    expandAll: {
      type: Boolean,
      default: false,
    },
    expandLimit: {
      type: [Number, Boolean],
      default: false,
    },
    items: {
      type: Array,
      default: () => ([]),
    },
    section: {
      type: String,
      default: '',
    },
    scrollTo: {
      type: Boolean,
      default: false,
    },
    titleClass: {
      type: String,
      default: 'h5',
    },
  },

  data() {
    return {
      global: true,
      settings: {
        singleOpen: true,
        expandInitial: false,
        expandAll: false,
        animate: true,
      },
      activeItems: [],
    }
  },

  mounted() {
    if (this.expandAll || this.expandLimit) {
      this.addActiveItems()
    }
  },

  methods: {

    /**
     * Check if the click target is an accordion control.
     * @param {HTMLElement} target - Clicked target to check quick view attributes against.
     * @returns {Boolean}
     */
    isTargetControl(target) {
      return (
        typeof target.attributes['aria-controls'] !== 'undefined'
      )
    },

    /**
     * Toggle targeted accordion item.
     * @param {HTMLElement} control - The clicked accordion control.
     */
    toggleItem(control) {
      const item = this.$refs[control]
      if (this.isItemExpanded(control)) {
        const { activeItems } = this
        const itemIndex = activeItems.indexOf(control)
        activeItems.splice(itemIndex, 1)

      } else {
        this.activeItems.push(control)
      }

      if (this.settings.scrollTo) {
        this.setWindowScroll(item)
      }
    },

    /**
     * Check if item is active.
     * @param {HTMLElement} control - The accordion item.
     * @returns {Boolean}
     */
    isItemExpanded(control) {
      return this.activeItems.includes(control)
    },

    addActiveItems() {
      this.items.map((el, index) => {
        if (this.expandAll || (this.expandLimit && index < this.expandLimit)) {
          this.activeItems.push(`${this.section}-${index}`)
        }
      })
    },

   /**
   * Set window scroll
   * @param {Object} target - The returned element that was clicked
   */
    setWindowScroll(target) {
      if (!window.matchMedia(`(min-width: ${breakpoints.l})`).matches) {
          target.item.scrollIntoView({
            behavior: 'smooth',
          })
      }
    },
  },
}
</script>
